.work{
    margin: 4rem 6rem;
    color: #2a2a2a;

}
.work h1{
    font-size: 3rem;
}
.workCard{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}
.workDaCard{
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem .8rem;
}
.workImage{
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
}
.workImage img{
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;

}
.workImage:hover img{
    transform: scale(1.3);
}
.workDaCard h4{
    font-size: 1.3rem;
    padding: .9rem 0 .2rem 0;
}
@media screen and (max-width:850px) {
    .work{
        margin: 4rem 2rem;
    }
    .workCard{
        flex-direction: column;
    }
    .workDaCard{
        width: 100%;
        margin-bottom: 1.5rem;
    }
}